"use client";

import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { useRouter } from "next/navigation";
import { useAuthStore } from "../store/authStore";
import { useProfileStore } from "../store/profileStore";
import { profileApi } from "../lib/api";
import AnimatedBackground from "../components/AnimatedBackground";
import { ArrowRight, Brain, CheckCircle, Sparkles } from "lucide-react";

// First 4 steps are multiple-choice; last step is free-text
const MC_QUESTIONS = [
    {
        id: "experience",
        question: "What's your current experience level with Data Structures & Algorithms?",
        options: [
            { value: "complete_beginner", label: "Complete beginner — I don't know where to start" },
            { value: "some_basics", label: "I know the basics — arrays, loops, etc." },
            { value: "intermediate", label: "Intermediate — I can solve easy/medium problems" },
            { value: "advanced", label: "Advanced — I can tackle hard problems" },
        ],
    },
    {
        id: "goal",
        question: "What's your primary goal right now?",
        options: [
            { value: "crack_interviews", label: "Crack FAANG / product company interviews" },
            { value: "competitive_programming", label: "Competitive programming" },
            { value: "build_understanding", label: "Build genuine understanding (not just pass tests)" },
            { value: "college_exams", label: "Prepare for college exams / placements" },
        ],
    },
    {
        id: "biggest_challenge",
        question: "What's your biggest struggle with DSA?",
        options: [
            { value: "coming_up_approach", label: "Coming up with the approach from scratch" },
            { value: "optimizing_brute_force", label: "Optimizing beyond brute-force" },
            { value: "dp_graph", label: "DP and graph problems specifically" },
            { value: "forgetting", label: "Memorizing solutions — I forget them too fast" },
        ],
    },
    {
        id: "time_availability",
        question: "How much time can you dedicate to learning per day?",
        options: [
            { value: "15_30", label: "15–30 minutes — I'm busy" },
            { value: "1_hour", label: "About an hour" },
            { value: "2_3_hours", label: "2–3 hours — serious grind" },
            { value: "more_than_3", label: "3+ hours — full-time focus" },
        ],
    },
];

const TOTAL_STEPS = MC_QUESTIONS.length + 1; // +1 for the free-text step

export default function OnboardingPage() {
    const router = useRouter();
    const { isAuthenticated } = useAuthStore();
    const { setProfile } = useProfileStore();
    const [step, setStep] = useState(0);
    const [answers, setAnswers] = useState<Record<string, string>>({});
    const [aboutText, setAboutText] = useState("");
    const [loading, setLoading] = useState(false);
    const [done, setDone] = useState(false);

    useEffect(() => {
        if (!isAuthenticated) router.push("/login");
    }, [isAuthenticated, router]);

    const isFreeTextStep = step === MC_QUESTIONS.length;
    const currentQ = isFreeTextStep ? null : MC_QUESTIONS[step];
    const progress = ((step + 1) / TOTAL_STEPS) * 100;

    const canProceed = isFreeTextStep
        ? aboutText.trim().length > 0
        : !!answers[currentQ!.id];

    const selectOption = (val: string) => {
        setAnswers((prev) => ({ ...prev, [currentQ!.id]: val }));
    };

    const handleNext = async () => {
        if (!canProceed) return;

        if (!isFreeTextStep) {
            setStep(step + 1);
            return;
        }

        // Last step — submit
        setLoading(true);
        const payload = { ...answers, about: aboutText };
        try {
            const { data } = await profileApi.completeOnboarding(payload);
            setProfile(data.profile || data);
            setDone(true);
            setTimeout(() => router.push("/dashboard"), 2000);
        } catch {
            router.push("/dashboard");
        } finally {
            setLoading(false);
        }
    };

    if (done) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-[#faf9f6] relative">
                <AnimatedBackground />
                <motion.div
                    className="relative z-10 text-center"
                    initial={{ opacity: 0, scale: 0.8 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ duration: 0.5 }}
                >
                    <div className="w-20 h-20 bg-[#f97316] rounded-full flex items-center justify-center mx-auto mb-6">
                        <CheckCircle className="w-10 h-10 text-white" />
                    </div>
                    <h2 className="font-serif font-bold text-3xl text-[#0d0d0d] mb-3">Profile created!</h2>
                    <p className="text-[#6b7280]">Taking you to your personalized dashboard…</p>
                </motion.div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-[#faf9f6] flex flex-col relative">
            <AnimatedBackground />

            {/* Progress */}
            <div className="relative z-10 pt-20 px-6">
                <div className="max-w-2xl mx-auto">
                    <div className="flex items-center justify-between mb-2">
                        <span className="text-xs font-medium text-[#9ca3af] uppercase tracking-widest">
                            Onboarding
                        </span>
                        <span className="text-xs font-medium text-[#9ca3af]">
                            {step + 1} / {TOTAL_STEPS}
                        </span>
                    </div>
                    <div className="h-1 bg-[#e5e7eb] rounded-full overflow-hidden">
                        <motion.div
                            className="h-full bg-[#f97316] rounded-full"
                            animate={{ width: `${progress}%` }}
                            transition={{ duration: 0.4 }}
                        />
                    </div>
                </div>
            </div>

            {/* Step content */}
            <div className="flex-1 flex items-center justify-center px-6 py-12 relative z-10">
                <div className="max-w-2xl w-full">
                    <AnimatePresence mode="wait">
                        <motion.div
                            key={step}
                            initial={{ opacity: 0, x: 30 }}
                            animate={{ opacity: 1, x: 0 }}
                            exit={{ opacity: 0, x: -30 }}
                            transition={{ duration: 0.35 }}
                        >
                            <div className="flex items-center gap-2 mb-6">
                                <Brain className="w-5 h-5 text-[#f97316]" />
                                <span className="text-xs font-bold text-[#f97316] tracking-widest uppercase">
                                    Building Your Cognitive Profile
                                </span>
                            </div>

                            {/* Multiple-choice step */}
                            {!isFreeTextStep && currentQ && (
                                <>
                                    <h2 className="font-serif font-bold text-3xl md:text-4xl text-[#0d0d0d] mb-10 leading-tight">
                                        {currentQ.question}
                                    </h2>
                                    <div className="flex flex-col gap-3">
                                        {currentQ.options.map((opt) => (
                                            <motion.button
                                                key={opt.value}
                                                onClick={() => selectOption(opt.value)}
                                                className={`w-full text-left px-6 py-4 rounded-xl border-2 transition-all text-sm font-medium ${answers[currentQ.id] === opt.value
                                                        ? "border-[#f97316] bg-[#f97316]/5 text-[#0d0d0d]"
                                                        : "border-[#e5e7eb] bg-white text-[#374151] hover:border-[#d1d5db]"
                                                    }`}
                                                whileHover={{ scale: 1.01 }}
                                                whileTap={{ scale: 0.99 }}
                                            >
                                                <div className="flex items-center justify-between">
                                                    <span>{opt.label}</span>
                                                    {answers[currentQ.id] === opt.value && (
                                                        <CheckCircle className="w-4 h-4 text-[#f97316] shrink-0" />
                                                    )}
                                                </div>
                                            </motion.button>
                                        ))}
                                    </div>
                                </>
                            )}

                            {/* Free-text step */}
                            {isFreeTextStep && (
                                <>
                                    <h2 className="font-serif font-bold text-3xl md:text-4xl text-[#0d0d0d] mb-3 leading-tight">
                                        Tell us about yourself.
                                    </h2>
                                    <p className="text-[#6b7280] text-sm mb-8 leading-relaxed">
                                        Your background, what you've tried before, how you think about problems — anything that helps us personalise your experience. There are no wrong answers.
                                    </p>
                                    <textarea
                                        value={aboutText}
                                        onChange={(e) => setAboutText(e.target.value)}
                                        placeholder="e.g. I'm a 3rd-year CS student. I've done some LeetCode but always get stuck on DP. I tend to rush into coding before fully understanding the problem…"
                                        rows={6}
                                        className="w-full border-2 border-[#e5e7eb] rounded-xl px-5 py-4 text-sm text-[#374151] bg-white outline-none focus:border-[#f97316] transition-colors placeholder:text-[#c4c4c4] resize-none leading-relaxed"
                                    />
                                    <p className="text-xs text-[#9ca3af] mt-2">
                                        {aboutText.trim().length} / 1000 characters
                                    </p>
                                </>
                            )}

                            {/* Next / Submit button */}
                            <motion.button
                                onClick={handleNext}
                                disabled={!canProceed || loading}
                                className={`mt-8 btn-accent flex items-center gap-2 px-8 py-3.5 ${!canProceed ? "opacity-40 cursor-not-allowed" : ""
                                    }`}
                                whileHover={canProceed ? { scale: 1.02 } : {}}
                                whileTap={canProceed ? { scale: 0.98 } : {}}
                            >
                                {loading ? (
                                    <span className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                                ) : (
                                    <>
                                        {!isFreeTextStep ? "Next Question" : "Build My Profile"}
                                        {!isFreeTextStep
                                            ? <ArrowRight className="w-4 h-4" />
                                            : <Sparkles className="w-4 h-4" />
                                        }
                                    </>
                                )}
                            </motion.button>
                        </motion.div>
                    </AnimatePresence>
                </div>
            </div>
        </div>
    );
}
