import axios from "axios";

const API_BASE = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";

const api = axios.create({
    baseURL: API_BASE,
    headers: { "Content-Type": "application/json" },
});

// Attach JWT on every request
api.interceptors.request.use((config) => {
    if (typeof window !== "undefined") {
        const token = localStorage.getItem("perspectra_token");
        if (token) {
            config.headers.Authorization = `Bearer ${token}`;
        }
    }
    return config;
});

// Redirect to /login on 401
api.interceptors.response.use(
    (res) => res,
    (err) => {
        if (err.response?.status === 401 && typeof window !== "undefined") {
            localStorage.removeItem("perspectra_token");
            window.location.href = "/login";
        }
        return Promise.reject(err);
    }
);

export default api;

// ─── Auth ──────────────────────────────────────────────────────────────────
export const authApi = {
    register: (data: { name: string; email: string; password: string }) =>
        api.post("/auth/register", data),
    login: (data: { email: string; password: string }) =>
        api.post("/auth/login", data),
    me: () => api.get("/auth/me"),
};

// ─── Profile ──────────────────────────────────────────────────────────────
export const profileApi = {
    get: () => api.get("/profile/me"),
    completeOnboarding: (answers: Record<string, string>) =>
        api.post("/profile/onboarding", { answers }),
};

// ─── Problems ─────────────────────────────────────────────────────────────
export const problemsApi = {
    // Backend: GET /problems/list → { problems: [...], total: N }
    list: (params?: { difficulty?: number; concept?: string }) =>
        api.get("/problems/list", { params }).then((r) => ({ ...r, data: r.data.problems ?? [] })),
    // Backend: GET /problems/:id → { ...problem }
    get: (id: string) => api.get(`/problems/${id}`),
};

// ─── Review ───────────────────────────────────────────────────────────────
export const reviewApi = {
    submit: (data: { problem_id: string; code: string; language: string }) =>
        api.post("/review/submit", data),
    // Backend: GET /review/history → { reviews: [...] }
    history: () =>
        api.get("/review/history").then((r) => ({ ...r, data: r.data.reviews ?? [] })),
};

// ─── Tutor ────────────────────────────────────────────────────────────────
export const tutorApi = {
    ask: (data: { problem_id: string; question: string }) =>
        api.post("/tutor/ask", data),
    hint: (data: { problem_id: string }) => api.post("/tutor/hint", data),
};

// ─── Graph ────────────────────────────────────────────────────────────────
export const graphApi = {
    // Backend: GET /graph/concepts → { nodes: [...], edges: [...] }
    concepts: () =>
        api.get("/graph/concepts").then((r) => ({ ...r, data: r.data.nodes ?? [] })),
    // Backend: GET /graph/recommend → { recommendations: [...] }
    recommendations: () =>
        api.get("/graph/recommend").then((r) => ({ ...r, data: r.data.recommendations ?? [] })),
};

// ─── Insights ─────────────────────────────────────────────────────────────
export const insightsApi = {
    get: () => api.get("/insights/me"),
};
